.navbar {
    background-color: #fd7400;
    -webkit-box-shadow: 0px 8px 8px #888888;
            box-shadow: 0px 8px 8px #888888;
    color: white;
    font-size: 2rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
    text-shadow: 1.5px 1.5px black;
}

.navbar-brand {
    font-size: 2.5rem;
    font-weight: 700;
}

.navbar-brand a {
    color: white;
    text-shadow: 1.5px 1.5px black;
}

@media screen and (min-width: 414px) {
  .navbar {
    height: 120px;
    font-size: 1.0rem;
  }
  .navbar-brand {
    font-size: 1.5rem;
  }
}

@media screen and (min-width: 736px) {
    .navbar {
      height: 75px;
    }
  }




